OneWire.allow({
	insert: function (userId, doc) {
		return OneWire.userCanInsert(userId, doc);
	},

	update: function (userId, doc, fields, modifier) {
		return OneWire.userCanUpdate(userId, doc);
	},

	remove: function (userId, doc) {
		return OneWire.userCanRemove(userId, doc);
	}
});

OneWire.before.insert(function(userId, doc) {
	doc.createdAt = new Date();
	doc.createdBy = userId;
	doc.modifiedAt = doc.createdAt;
	doc.modifiedBy = doc.createdBy;

	
	if(!doc.createdBy) doc.createdBy = userId;
});

OneWire.before.update(function(userId, doc, fieldNames, modifier, options) {
	modifier.$set = modifier.$set || {};
	modifier.$set.modifiedAt = new Date();
	modifier.$set.modifiedBy = userId;

	
});

OneWire.before.remove(function(userId, doc) {
	
});

OneWire.after.insert(function(userId, doc) {
	
});

OneWire.after.update(function(userId, doc, fieldNames, modifier, options) {
	console.log("OneWire - afterUpdate");
	var message = doc.message;
	var sensorArr = [];
	var timestamp = new Date(message["time"]);
	for(var key in message){
		if(key !== "time"){
			  var sensorid = key;
			  for(var valuekey in message[key]){
			    var tempObj = {};  
			    tempObj["signal"] = sensorid+"."+valuekey;
			    tempObj["value"] = message[key][valuekey];
			    tempObj["timestamp"] = timestamp;
			    sensorArr.push(tempObj);
			  }
		}
	}

	sensorArr.forEach(function(sensor,i){
		var unit = "";
		if(sensor.signal.indexOf("tempC") > -1){
			unit = "Â°C";
		}
		else if(sensor.signal.indexOf("counter") > -1){
			unit = "#";
		}
		else if(sensor.signal.indexOf("hum") > -1){
			unit = "%RH";
		}
		var searchSignal = Signals.findOne({"signal":sensor.signal});
		if(searchSignal !== undefined){
			var offsetA = searchSignal.offsetA;
			var offsetB = searchSignal.offsetB;
			var scale = searchSignal.scale;
			Signals.upsert({"signal":sensor.signal},{$set:{value: scale*(sensor.value+offsetA)+offsetB, raw: sensor.value, timestamp: sensor.timestamp}});
		}
		else{
			Signals.upsert({"signal":sensor.signal},{$set:{name: null,bus: "onewire",value: sensor.value, raw: sensor.value, timestamp: sensor.timestamp, unit: unit, offsetA: 0.0, offsetB: 0.0, scale: 1.0, hidden: false}});
		}
	});
});

OneWire.after.remove(function(userId, doc) {
	
});
